
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import {verifyPhone} from '../../utils/index.js'
export default {
name: '',
components: {
Group,
XInput,
XButton,
XTextarea,
Toast,
XAddress,
CheckIcon
},
data () {
return {
type: '',
detail: {},
toastData: {
isShow: false,
type: 'warn',
text: ''
},
addressData: ChinaAddressData,
addressValue: [],
onLoadShow: true,
json: {
address: '',
pname: '',
cname: '',
rname: '',
mobile: null,
recive: '',
default: true
}
}
},
created () {
let id = this.$route.params.id
this.type = this.$route.params.type
if (id && id !== 'null') {
this.json.id = id
this.getDetail(id)
}
},
methods: {
getDetail (id) {
let _this = this
this.axios.post('member/addresslist', {
order: 'ASC',
pageNum: 1,
pageSize: 100
}).then((response) => {
response.data.some((item, index) => {
if (item.id === id) {
_this.addressValue = [item.pname, item.cname, item.rname]
_this.json.address = item.address
_this.json.pname = item.pname
_this.json.cname = item.cname
_this.json.rname = item.rname
_this.json.id = item.id
_this.json.mobile = item.mobile
_this.json.recive = item.recive
console.log(_this.detail)
}
})
})
},
onShadowChange (ids, names) { // 省市区改变
this.json.pname = names[0]
this.json.cname = names[1]
this.json.rname = names[2]
this.addressValue = [names[0], names[1], names[2]]
},
hide (bool) {
if (!bool && (this.json.pname === '省')) { // 如果取消
this.onLoadShow = true
}
},
show () {
console.log('show')
},
save () {
let json = this.json
let text = ''
if (!json.recive) {
text = '请填写收货人'
} else if (JSON.stringify(this.addressValue) === '[]') {
text = '请选择地址'
} else if (!json.address) {
text = '请填写详细地址'
} else if (!json.mobile || !verifyPhone(json.mobile)) {
text = '请填写正确的手机号'
} else {
console.log(this.json.id)
if (this.json.id) { // 修改地址
console.log('c')
this.axios.post('member/addressedit', this.json).then((response) => {
if (response.success) {
text = '修改成功'
this.link(text)
}
})
} else { // 添加地址
console.log('add')
this.axios.post('member/addressadd', json).then((response) => {
if (response.success) {
text = '添加成功'
this.link(text)
}
})
}
return false
}
this.toastData.type = 'warn'
this.toastData.text = text
this.toastData.isShow = true
console.log('save', text)
},
link (text) {
let _this = this
_this.toastData.text = text
_this.toastData.isShow = true
_this.toastData.type = 'success'
let timer = setTimeout(() => {
this.$router.push({name: 'address', params: {type: this.type}})
clearTimeout(timer)
return false
}, 1000)
}
}
}



